.gpt3-cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  margin: 4rem;
  border-radius: 1rem;
  background: var(--gradient-bar);
  border: 1px solid #000000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.gpt3-cta-content p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 30px;
  color: #0e0e0e;
}

.gpt3-cta-content h3 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 24px;
  line-height: 45px;
  color: #000000;
}

.gpt3-cta-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 2rem;
}

.gpt3-cta-btn button {
  background: #000000;
  color: #ffffff;
  border-radius: 40px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  padding: 0.5rem 1rem;
  border: none;
  outline: none;
  cursor: pointer;
  min-width: 150px;
}

@media screen and (max-width:650px) {
  .gpt3-cta {
    flex-direction: column;
  }
  .gpt3-cta-btn {
    margin: 2rem 0 0;
  }
}


@media screen and (max-width:550px) {
  .gpt3-cta {
    margin: 4rem 2rem;
  }
  .gpt3-cta-content h3 {
    font-size: 18px;
    line-height: 32px;
  }
  .gpt3-cta-btn button {
    font-size: 14px;
    line-height: 28px;
  }
}